<template>
    <yd-layout class="secretaryDo">
        <yd-tab active-color="#333" :callback="callback">
            <yd-tab-panel label="送领导审批">
                <div class="mainContentFluid">
                    <div class="approval">
                        选择审批人
                    </div>
                    <div class="fluidWidth">
                        <div class="tripAppro">
                            <div class="name_circle">
                                <ul>
                                    <li>
                                        <div class="lastname" v-bind:class="{'check':leaderNum == 1}" v-on:click="leaderNum=1">
                                            {{leaderName.substring(0,1)}}
                                            <div class="nameClick">
                                            </div>
                                            <div class="imgClick">
                                            </div>
                                        </div>
                                        <div class="name">
                                            {{leaderName}}
                                        </div>
                                    </li>
                                </ul>
                                <div class="clear"></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="reason">
                    <div class="reasonTitle">填写附言</div>
                    <trsTextarea placeholder="附言最多为100汉字..." v-model="remarks"></trsTextarea>
                </div>
            </yd-tab-panel>
            <yd-tab-panel label="代领导录入">
                <div class="mainContentFluid">
                    <div class="approval">
                        常用意见
                    </div>
                    <div class="sugg_text">
                        <div class="oneText" :class="{'suggActive':suggNum==1}" @click="suggNum=1;commonAdvice='同意'">同意</div>
                        <div class="oneText twoText" :class="{'suggActive':suggNum==2}" @click="suggNum=2;commonAdvice='不同意'">不同意</div>
                        <div class="oneText threeText" :class="{'suggActive':suggNum==3}" @click="suggNum=3;commonAdvice='返回修改'">返回修改</div>
                    </div>
                </div>
                <div class="reason">
                    <div class="reasonTitle">审批意见</div>
                    <trsTextarea placeholder="请输入您的意见" v-model="commonAdvice"></trsTextarea>
                </div>
            </yd-tab-panel>
        </yd-tab>
        <yd-tabbar slot="tabbar">
            <div class="sendLeader" @click="tabKey==0?sendLeader():agentLeader()">发送</div>
            <div class="submit" @click="$router.go(-1)">返回</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'secretaryDo',
    data() {
        return {
            tabKey: 0,
            leaderNum: 1,
            suggNum: 0,
            commonAdvice: '',
            remarks: '',
            leaderName: ''
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('业务审批');
        vm.leaderName = vm.getMishuInfo.leaderName;
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getMishuInfo'])
    },
    methods: {
        sendLeader() {
            let vm = this;
            if (vm.leaderNum == 999) {
                vm.$dialog.toast({
                    mes: '请选择审批人',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'crossSystemApproval',
                    'workItemID': vm.$route.params.workId,
                    'activityID': 'leaderAppActivity',
                    'leaderIDs': vm.getMishuInfo.leaderId,
                    'fuyan': vm.remarks,
                    'opinion': '',
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'workTitle': vm.getMishuInfo.reqdeptname + vm.getMishuInfo.requsername + '业务审批',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            }
        },
        agentLeader() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                'type': 'crossSystemApproval',
                'workItemID': vm.$route.params.workId,
                'activityID': 'secrataryAppActivity2',
                'leaderIDs': vm.getMishuInfo.sec2LeaderId,
                'fuyan': '',
                'opinion': vm.commonAdvice,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'workTitle': vm.getMishuInfo.reqdeptname + vm.getMishuInfo.requsername + '业务审批',
                'ext': ''
            }, function(res) {
                vm.$dialog.toast({
                    mes: '审批完成',
                    timeout: 2000,
                    callback: () => {
                        vm.$router.push('/handle/');
                    }
                });
            });
        },
        callback(label, key) {
            let vm = this;
            vm.tabKey = key;
        }
    }
}
</script>
<style lang="less" scoped>
.mainContentFluid,
.reason {
    border-top: .2rem solid #f8f8f8;
    margin-top: 0;
}

.fluidWidth {
    padding-top: 0;
    padding-bottom: 0;
}

.approval {
    height: 1rem;
    line-height: 1rem;
    color: #333;
    font-size: .32rem;
    padding-left: .38rem;
}

.submit {
    background-color: #a4ceff;
}

.reason {
    padding-left: 0.38rem;
    padding-right: .28rem;
    background-color: #fff;
    margin-top: .14rem;
    min-height: 2rem;
    .sqReason {
        font-size: .32rem;
        color: #666;
        line-height: .6rem;
        min-height: 1rem;
        word-break: break-all;
    }
    .reasonTitle {
        height: 1rem;
        line-height: 1rem;
        font-size: .32rem;
        color: #333;
        font-weight: bold;
    }
    .trsTextarea {
        font-size: 0.32rem;
        padding-right: 0.2rem;
        color: #666;
        height: 2.1rem;
    }
}

.sugg_text {
    height: .56rem;
    padding-left: .38rem;
    display: flex;
    justify-content: flex-start;
    margin-bottom: .4rem;
    .oneText {
        border: 1px solid #999;
        padding: 0 0.2rem;
        font-size: .32rem;
        color: #999;
        border-radius: 20px;
        line-height: .56rem;
        width: 1.3rem;
        text-align: center;
    }
    .twoText {
        width: 1.6rem;
        margin-left: .4rem;
    }
    .threeText {
        width: 2rem;
        margin-left: .4rem;
    }
}

.suggActive {
    background-color: #59a5fd;
    border: none!important;
    color: #fff!important;
}

.sendLeader {
    width: 50%;
    color: #fff;
    font-size: .36rem;
    line-height: 1rem;
    text-align: center;
    background-color: #59a5fd;
}

.submit {
    width: 50%;
    color: #fff;
    font-size: .36rem;
    line-height: 1rem;
    text-align: center;
    background-color: #a4ceff;
}

.check .nameClick {
    width: 0.81rem;
    height: 0.81rem;
    margin: 0 auto;
    border-radius: 1rem;
    text-align: center;
    z-index: 2;
    opacity: 0.5;
    background-color: #256f7b;
    position: absolute;
    top: 0;
    right: 0;
}

.check .imgClick {
    width: 0.81rem;
    height: 0.81rem;
    margin: 0 auto;
    border-radius: 1rem;
    text-align: center;
    z-index: 99;
    position: absolute;
    top: 11px;
    right: -6px;
    background-image: url(../../../static/images/gou.png);
    background-repeat: no-repeat;
    background-size: 0.6rem;
}
</style>


